import socketService from "@/services/socket.service"
import { useStore } from "@/store"

export default function useChatMessageListener() {
  const chatStore = useStore("Chat")
  const userStore = useStore("User")
  socketService.getSocket().off("sent-message")
  socketService
    .getSocket()
    .on(
      "sent-message",
      ({ partnerUserId, createdAt, messageClientId, _id }) => {
        chatStore.updateMessage({
          messageClientId,
          _id,
          partnerUserId,
          createdAt,
        })
      }
    )
  socketService.getSocket().on("received-message", ({ ...message }) => {
    const currentUserId = userStore.currentUser._id
    chatStore.createMessage({
      ...message,
      isPending: false,
      currentUserId,
    })
  })
}
