<template>
	<div class="chat main">
		<div v-if="isLoggedIn" class="columns is-gapless is-fullheight">
			<div class="column is-3">
				<TheChatList />
			</div>
			<div class="column">
				<base-trans
					appear
					appearClass="fadeInRight"
					enterClass="fadeInRight"
					extraClass="animate__faster"
				>
					<TheChatBox v-show="currentPartnerUsername" />
				</base-trans>
			</div>
		</div>
		<div class="is-fullheight" v-else>
			<TheUnauthorisedAlert />
		</div>
	</div>
</template>

<script>
import TheChatList from "@/components/TheChatList/TheChatList.vue"
import TheChatBox from "@/components/TheChatBox/TheChatBox.vue"
import TheUnauthorisedAlert from "@/components/TheUnauthorisedAlert/TheUnauthorisedAlert.vue"
import { useStore } from "@/store"
import { useRouter } from "vue-router"
import { watchEffect } from "vue"
export default {
	components: {
		TheChatList,
		TheChatBox,
		TheUnauthorisedAlert,
	},
	setup() {
		const userStore = useStore("User")

		const isLoggedIn = userStore.isLoggedIn

		const router = useRouter()

		const chatStore = useStore("Chat")

		const { updateCurrentPartnerByRoute, currentPartnerUsername } = chatStore

		watchEffect(() => {
			const {
				partnerUsername,
			} = router.currentRoute.value.params
			if (!partnerUsername) return
			updateCurrentPartnerByRoute(partnerUsername)
		})

		return {
			isLoggedIn,
			currentPartnerUsername,
		}
	},
}
</script>

