import { useStore } from "@/store"

export default (root, loadingElement) => {
  let options = {
    root: root.value,
    rootMargin: "0px",
    threshold: 0,
  }

  const chatStore = useStore("Chat")

  let callback = (entries, observer) => {
    entries.forEach(async (entry) => {
      if (entry.isIntersecting) {
        const { currentPartnerUsername, conversations } = chatStore
        const conversation = conversations[currentPartnerUsername.value]
        if(!conversation) return
        if (conversation.isFetchingNewMessages || conversation.isFullLoaded) return
        conversation.isFetchingNewMessages = true
        await chatStore.fetchNewMessages(currentPartnerUsername.value)
        conversation.isFetchingNewMessages = false
      }
    })
  }

  let observer = new IntersectionObserver(callback, options)

  observer.observe(loadingElement.value.$el)
}
