<template>
	<ul class="chat-list">
		<TheChatListItem
			v-for="contact in recentContacts"
			:key="contact._id"
			:username="contact.partner?.username"
			:displayName="contact.partner?.profile.displayName"
			:recentMessageContent="contact.content"
			:createdAt="contact.createdAt"
			:userId="contact.partner?._id"
		/>
	</ul>
</template>

<script>
import TheChatListItem from "@/components/TheChatListItem/TheChatListItem.vue"
import { useStore } from "@/store"
export default {
	components: { TheChatListItem },
	setup() {
		const chatStore = useStore("Chat")

		const { fetchRecentContacts, recentContacts } = chatStore

		fetchRecentContacts({
			offset: 0,
		})

		return {
			recentContacts,
		}
	},
}
</script>

<style scoped src="./styles.css">
</style>