<template>
	<div
		class="chat-message"
		:class="[sender, { 'is-pending': isPending }]"
		:title="formattedDate"
	>
		<p class="content">{{ content }}</p>
	</div>
</template>

<script>
import { toRefs } from "vue"
import moment from "moment"
export default {
	props: {
		sender: {
			type: String,
			required: true,
		},
		content: {
			type: String,
			required: true,
		},
		createdAt: {
			type: String,
		},
		isPending: {
			type: Boolean,
		},
	},
	setup(props) {
		const { createdAt } = toRefs(props)

		const formattedDate = moment
			.utc(createdAt.value)
			.local()
			.format("HH:mm:ss DD/MM/YYYY")

		return {
			formattedDate,
		}
	},
}
</script>

<style scoped src="./styles.css">
</style>