<template>
	<div class="chat-input">
		<div class="columns is-vcentered is-fullwidth is-gapless">
			<div class="column is-narrow">
				<div class="toolbar">
					<a class="photo-video tool">
						<BaseIcon class="is-medium" icon="las la-photo-video" />
					</a>
					<a class="file tool">
						<BaseIcon class="is-medium" icon="las la-paperclip" />
					</a>
				</div>
			</div>
			<div class="column">
				<div class="input-wrapper">
					<form @submit.prevent="sendMessage">
						<input
							v-model="messageContent"
							class="message-input input"
							type="text"
							placeholder="Write your message here..."
						/>
					</form>
				</div>
			</div>
			<div class="column is-narrow">
				<div class="toolbar pr-4">
					<a href="#" class="emoji tool">
						<BaseIcon class="is-medium" icon="las la-smile-beam" />
					</a>
				</div>
			</div>
			<div class="column is-narrow">
				<a @click.prevent="sendMessage" class="is-circle send-button">
					<BaseIcon icon="las la-paper-plane" />
				</a>
			</div>
		</div>
	</div>
</template>

<script>
import { ref } from "vue"
import socketService from "@/services/socket.service"
import { useStore } from "@/store"
import { find } from "lodash"
import moment from "moment"
export default {
	setup() {
		const messageContent = ref("")
		const chatStore = useStore("Chat")
		const { conversations, createNewMessage } = chatStore

		const userStore = useStore("User")

		const sendMessage = () => {
			const { currentPartnerUsername } = chatStore

			const token = userStore.currentUser.token.value
			const receiverUsername = currentPartnerUsername
			const receiverId = find(conversations, {
				username: receiverUsername,
			})._id

			const currentUserId = userStore.currentUser._id.value

			const message = {
				content: messageContent.value,
				senderId: currentUserId,
				receiverId,
				createdAt: moment().format(),
			}

			let messageFakeId = createNewMessage({
				...message,
				currentUserId,
			})

			socketService.sendMessage({
				token,
				to: receiverId,
				content: messageContent.value,
				messageClientId,
			})

			messageContent.value = ""
		}

		return {
			sendMessage,
			messageContent,
		}
	},
}
</script>

<style src="./styles.css">
</style>